body{
  overflow: hidden;
}
/* ------- Loader ------- */
#loader{
  z-index: 1;
  background: rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

#animation{
  width: 45px;
  height: 45px;
  border: 3px solid rgb(0, 16, 34);
  animation: loader 2s infinite ease;
}

#animation-inner{
  width: 100%;
  background: rgb(0, 16, 34);
  animation: loader-inner 2s infinite ease-in;
}

@keyframes loader{
  0% {transform: rotate(0deg);}
  25% {transform: rotate(180deg);}
  50% {transform: rotate(180deg);}
  75% {transform: rotate(360deg);}
  100% {transform: rotate(360deg);}
}

@keyframes loader-inner{
  0% {height: 0%;}
  25% {height: 0%;}
  50% {height: 100%;}
  75% {height: 100%;}
  100% {height: 0%;}
}

/* ------- Header ------- */
#main-header{
  background:rgb(0, 4, 8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color: white;
  z-index: 1;
}

#left-side{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  width: 50%;
}

#logo-menu{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  gap: 10px;
}

/* ------- Sidebar ------- */
#sidebar{
  width: 100px;
  height: 100vh;
  display: fixed;
  top: 80px;
  left: 0;
  background: rgba(0, 0, 0, 0);
  color: white;
}